import { Service } from './service'
import { RUNTIME_MESSAGE } from '../constant'

export const GA_ACTIONS = {
  EVENT: 'event',
  ERROR: 'error'
}

export class GAService extends Service {
  static async event (event) {
    return await this.message({ action: RUNTIME_MESSAGE.GOOGLE_ANALYTICS, event, GAAction: GA_ACTIONS.EVENT })
  }

  static async error (error) {
    return await this.message({ action: RUNTIME_MESSAGE.GOOGLE_ANALYTICS, error, GAAction: GA_ACTIONS.ERROR })
  }
}
