import { RUNTIME_MESSAGE } from '../constant'
import { Service } from './service'

export const LOCAL_STORAGE_ACTIONS = {
  GET: 'getItem',
  SET: 'setItem',
  REMOVE: 'removeItem'
}

export class StorageService extends Service {
  static async getItem (key, fallback) {
    return await this.message({ action: RUNTIME_MESSAGE.LOCAL_STORAGE, localStorageAction: LOCAL_STORAGE_ACTIONS.GET, key, fallback })
  }

  static async setItem (key, value) {
    return await this.message({ action: RUNTIME_MESSAGE.LOCAL_STORAGE, localStorageAction: LOCAL_STORAGE_ACTIONS.SET, key, value })
  }

  static async removeItem (key) {
    return await this.message({ action: RUNTIME_MESSAGE.LOCAL_STORAGE, localStorageAction: LOCAL_STORAGE_ACTIONS.REMOVE, key })
  }
}
