import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

const Colors = {
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  SUCCESS: 'success',
  DANGER: 'danger',
  WARNING: 'warning',
  INFO: 'info',
  LIGHT: 'light',
  DARK: 'dark'
}

const defaultColors = [
  Colors.PRIMARY,
  Colors.SECONDARY,
  Colors.SUCCESS,
  Colors.DANGER,
  Colors.WARNING,
  Colors.INFO,
  Colors.LIGHT,
  Colors.DARK
]

const Loading = ({ interval, colors, children, ...props }) => {
  const [index, setIndex] = useState(0)

  useEffect(() => {
    const tick = () => {
      setIndex((prevIndex) => (prevIndex >= colors.length ? 0 : prevIndex + 1))
    }
    const timer = setInterval(tick, interval)
    return () => {
      clearInterval(timer)
    }
  }, [interval, colors])

  return (
    <div {...props}>
      <div className={'spinner-border text-' + colors[index]} role='status'>
        {children || <span className='sr-only'>Loading...</span>}
      </div>
    </div>
  )
}

Loading.propTypes = {
  interval: PropTypes.number,
  colors: PropTypes.array,
  children: PropTypes.element
}
Loading.defaultProps = {
  interval: 500,
  colors: defaultColors
}

export default Loading
