import { IS_FIREFOX } from '../helpers'
import { generateGuid } from '../utilities'

const resolves = {}
const rejects = {}

if (IS_FIREFOX) {
  // Firefox
  window.addEventListener('message', (message) => {
    const { data, origin } = message
    const { direction, response, error, guid } = data
    if (direction === 'content_scripts') {
      const matches = window.react_env.REACT_APP_EXTERNALLY_CONNECTABLE.split(' ').filter((url) => origin.match(url))
      if (matches.length !== 0) {
        response ? resolves[guid](response) : rejects[guid](error)
      } else {
        rejects[guid]('origin error')
      }
    }
  }, false)
}

/**
 * do set react chrom extension id in global window object
 */
export class Service {
  static messageChrome (message) {
    return new Promise((resolve, reject) => {
      const responseFunc = (response) => {
        if (window.chrome.runtime.lastError || response.error) {
          reject(window.chrome.runtime.lastError || response.error)
        } else {
          resolve(response.result)
        }
      }
      if (window.react_env) {
        window.chrome.runtime.sendMessage(window.react_env.REACT_APP_CHROME_EXTENSION_ID, message, responseFunc)
      } else {
        window.chrome.runtime.sendMessage(message, responseFunc)
      }
    })
  }

  static messageFirefox (message) {
    const guid = generateGuid()
    // resolves[guid] = resolve
    // rejects[guid] = reject
    message.guid = guid
    window.postMessage({ direction: 'web_page', ...message }, '*')
  }

  static async message (message) {
    return await IS_FIREFOX ? this.messageFirefox(message) : this.messageChrome(message)
  }
}
